"use strict";
module.exports = function arrayToObject(o) {
    if (Array.isArray(o)) {
        const obj = {};
        // The purpose of this method is to commit type crimes
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        Object.keys(o).forEach(k => (obj[k] = o[k]));
        return obj;
    }
    return o;
};
